.dialogs {
    display: grid;
    grid-template-columns: 3fr 5fr;
    border-radius: 15px;
    background-color: #212437;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}
.dialogs_inner {
    padding: 10px;
}
.messages {
    padding: 10px;
}
.avatar {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
.avatar img {
    width: 50px;
    height: 50px;
    margin-right: 10px;
    border-radius: 50%;
}
.link {
    text-decoration: none;
    color: #fff;
}
.avatar a.active {
    color: #00d7b2;
}
